@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya+Sans+SC:wght@100&family=Almendra+Display&display=swap);
*{box-sizing:border-box}body{margin:0;padding:0}h1,h2{margin:0;padding:0}#home{width:100vw;height:100vh;display:flex;flex-direction:column;justify-content:center;align-items:center;grid-gap:30px;gap:30px;position:relative}#home.red{background:#533747}#home.red #quote-container #quote-content::before{background:#86bbbd}#home.red #quote-container #quote-content #quote::before,#home.red #quote-container #quote-content #quote::after{color:#76949f}#home.red #quote-container #author{color:#76949f}#home.red #quote-container #button{background:#5f506b}#home.red #quote-container #button:hover{background:rgba(95,80,107,0.5)}#home.indaco{background:#3a405a}#home.indaco #quote-container #quote-content::before{background:#f9dec9}#home.indaco #quote-container #quote-content #quote::before,#home.indaco #quote-container #quote-content #quote::after{color:#e9afa3}#home.indaco #quote-container #author{color:#e9afa3}#home.indaco #quote-container #button{background:rgba(174,197,235,0.8)}#home.indaco #quote-container #button:hover{background:rgba(174,197,235,0.3)}#home.blue{background:#2e6171}#home.blue #quote-container #quote-content::before{background:#f6fedb}#home.blue #quote-container #quote-content #quote::before,#home.blue #quote-container #quote-content #quote::after{color:#e6d3a3}#home.blue #quote-container #author{color:#e6d3a3}#home.blue #quote-container #button{background:#556f7a}#home.blue #quote-container #button:hover{background:rgba(85,111,122,0.5)}#home #quote-container{max-width:250px;position:relative;display:flex;flex-direction:column;justify-content:space-between;align-items:center;grid-gap:30px;gap:30px;z-index:1}#home #quote-container #quote-content{position:relative}#home #quote-container #quote-content #quote{position:relative;color:white;font-size:20px;font-family:"Alegreya Sans SC",sans-serif;line-height:40px;letter-spacing:1px}#home #quote-container #quote-content #quote::first-line{text-indent:25px}#home #quote-container #quote-content #quote::before,#home #quote-container #quote-content #quote::after{position:absolute;font-size:60px}#home #quote-container #quote-content #quote::before{content:"“";-webkit-transform:translate(-25px, 5px);transform:translate(-25px, 5px)}#home #quote-container #quote-content #quote::after{content:"”";-webkit-transform:translate(10px, 5px);transform:translate(10px, 5px)}#home #quote-container #author{font-size:18px;font-family:"Alegreya Sans SC",sans-serif}#home #quote-container #button{padding:10px 20px;color:white;font-size:20px;font-weight:bold;font-family:"Alegreya Sans SC",sans-serif;border:0;border-radius:5px;cursor:pointer;transition:500ms background ease}#home #image{width:200%;position:fixed;bottom:0;fill:rgba(0,0,0,0.7)}@media screen and (min-width: 768px){#home #quote-container{max-width:500px;flex-wrap:wrap;flex-direction:row}#home #quote-container #quote-content{flex:1 1 100%}#home #quote-container #quote-content::before{content:"";width:5px;height:100%;position:absolute;-webkit-transform:translateX(-20px);transform:translateX(-20px)}#home #quote-container #quote-content #quote{font-size:25px}#home #quote-container #author{font-size:20px}#home #quote-container #author::before{content:"❯";margin-right:10px}#home #image{width:120%}}

